.App {
  width: 50vw;
  margin: 10vh 25vw;
  float: left;
}

.App .box {
  padding: 16px;
}

.App .box .title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

.App .box select {
  padding: 5px;
  outline: none;
}

.App .box .content {
  border: 1px solid #ccc;
}

.App .box .content .itemList {
  border-bottom: 1px solid #ccc;
}

.App .box .content .itemList .nameList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 12px 12px 12px 36px;
  position: relative;
}

.App .box .content .itemList .nameList .btnToggle {
  position: absolute;
  top: 6px;
  left: 0;
  margin-top: 6px;
  width: 26px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #ccc;
  cursor: pointer;
}

.App .box .content .itemList .fa-caret-right {
  display: none;
}

.App .box .content .itemList.hide .fa-caret-right {
  display: block;
}

.App .box .content .itemList.hide .fa-caret-down {
  display: none;
}

.App .box .content .itemList .btnCheckbox {
  cursor: pointer;
}

.App .box .content .itemList .nameList span:nth-child(2) {
  flex: 1;
  font-size: 16px;
  font-weight: 600;
}

.App .box .content .itemList .boxItems {
  overflow: hidden;
  padding-bottom: 12px;
}

.App .box .content .itemList.hide .boxItems {
  height: 0;
  padding-bottom: 0;
}

.App .box .content .itemList .item {
  padding: 6px 12px 6px 36px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.App .box .content .itemList .item:hover {
  background-color: #ccc;
}

.App .box .content .itemList .item .hidden {
  opacity: 0;
}

.App .box .content .itemList .nameList .btnCheckbox .hidden,
.App .box .content .itemList .item:hover .hidden {
  opacity: 0.4;
  cursor: default;
}

.alert {
  display: flex;
  flex-direction: "row";
  border: 1px solid #ccc;
  overflow: hidden;
  border-radius: 5px;
  margin-bottom: 12px;
}

.alert span {
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b1012c;
  font-size: 24px;
  color: #fff;
}

.alert p {
  padding: 12px;
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.8);
}
